.page {
  color: #4f4f4f;
  scroll-behavior: smooth;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}

.page__body {
  background-color: #f8f8f8;
  min-width: 320px;
  margin: 0;
  padding: 0;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__header {
  margin-bottom: 116px;
}

@media (min-width: 640px) {
  .page__header {
    margin-bottom: 100px;
  }
}

@media (min-width: 1280px) {
  .page__header {
    margin-bottom: 148px;
  }
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: opacity .3s, transform .3s;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.page__menu:target {
  z-index: 2;
  opacity: 1;
  pointer-events: all;
  overflow: scroll;
  transform: translateX(0);
}

.container {
  box-sizing: border-box;
  min-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 30px;
  padding-right: 30px;
}

@media (min-width: 640px) {
  .container {
    max-width: 768px;
    padding-left: 34px;
    padding-right: 34px;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1440px;
    padding-left: 135px;
    padding-right: 135px;
  }
}

.header {
  padding-top: 16px;
}

@media (min-width: 640px) {
  .header {
    background-image: url("header-rectangle.55b5a586.png");
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: 80vw;
    padding-top: 24px;
  }
}

@media (min-width: 1280px) {
  .header {
    background-image: url("rectangle.fb264396.png");
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: 72vw;
    padding-top: 12px;
  }
}

.header__content {
  -ms-flex-direction: column;
  flex-direction: column;
  row-gap: 24px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
}

@media (min-width: 640px) {
  .header__content {
    display: block;
  }

  .header__top-bar {
    margin-bottom: 48px;
  }
}

@media (min-width: 1280px) {
  .header__top-bar {
    margin-bottom: 80px;
  }
}

@media (min-width: 640px) {
  .header__mob-bg {
    display: none;
  }
}

.header__mob-bg-img {
  width: 100%;
}

.header__show-more {
  bottom: -6px;
  right: -15px;
}

@media (min-width: 640px) {
  .header__show-more {
    bottom: 124px;
    left: 4px;
    right: auto;
  }
}

@media (min-width: 1280px) {
  .header__show-more {
    bottom: 176px;
  }
}

.link {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  text-decoration: none;
}

.icon {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 48px;
  height: 48px;
  transition: transform .3s;
}

.icon:hover {
  transform: scale(1.2);
}

.icon--menu {
  background-image: url("menu.9f067edf.svg");
}

@media (min-width: 1280px) {
  .icon--menu {
    display: none;
  }
}

.icon--basket {
  width: 30px;
  height: 30px;
  display: none;
}

@media (min-width: 1280px) {
  .icon--basket {
    display: block;
  }
}

.icon--basket svg {
  color: #000;
  transition: color .3s;
}

.icon--basket svg:hover {
  color: #eb5757;
}

.top-bar {
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-align: center;
  align-items: center;
  display: -ms-flexbox;
  display: flex;
}

.top-bar__link-logo {
  letter-spacing: .15em;
  letter-spacing: .06em;
  --text-color-main: #333;
  --text-color-hover: #03839e;
  color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(90deg, var(--text-color-hover) 50%, var(--text-color-main) 50%);
  background-position: 100%;
  background-size: 200%, 100%;
  -webkit-background-clip: text;
  background-clip: text;
  -ms-flex-item-align: center;
  align-self: center;
  font-family: Bebas Neue, sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 120%;
  transition: background-position .3s;
}

.top-bar__link-logo:hover {
  background-position: 0;
}

.nav {
  display: none;
}

@media (min-width: 1280px) {
  .nav {
    display: block;
  }
}

.nav__list {
  column-gap: 60px;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
}

.nav__link {
  --text-color-main: #4f4f4f;
  --text-color-hover: #eb5757;
  color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(90deg, var(--text-color-hover) 50%, var(--text-color-main) 50%);
  background-position: 100%;
  background-size: 200%, 100%;
  -webkit-background-clip: text;
  background-clip: text;
  transition: background-position .3s;
  position: relative;
}

.nav__link:hover {
  background-position: 0;
}

.nav__link:after {
  content: "";
  transform-origin: 0;
  border-bottom: 1px solid #eb5757;
  width: 100%;
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: -4px;
  transform: scale(0);
}

.nav__link:hover:after {
  transform: scale(1);
}

.bottom-header {
  position: relative;
}

@media (min-width: 640px) {
  .bottom-header {
    background-image: url("header-bg-mobile.54383a4b.png");
    background-position: 100% 0;
    background-repeat: no-repeat;
    background-size: 306px;
  }
}

@media (min-width: 640px) and (min-width: 1280px) {
  .bottom-header {
    background-image: url("header.e6b9e0f1.png");
    background-size: 500px;
    height: 500px;
  }
}

.bottom-header__wrapper {
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
  display: grid;
}

@media (min-width: 640px) {
  .bottom-header__wrapper {
    grid-template-columns: repeat(6, 1fr);
  }
}

@media (min-width: 1280px) {
  .bottom-header__wrapper {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
  }
}

.bottom-header__title {
  color: #03839e;
  letter-spacing: .15em;
  grid-column: span 2;
  margin: 0 0 24px;
  font-family: Bebas Neue, sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 120%;
}

.bottom-header__title--outline {
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 1px #03839e;
}

@media (min-width: 640px) {
  .bottom-header__title {
    grid-column: 1 / 7;
    margin-bottom: -30px;
    font-size: 64px;
    line-height: 64px;
  }
}

@media (min-width: 1280px) {
  .bottom-header__title {
    grid-column: 2 / 7;
    margin-bottom: 20px;
  }
}

.bottom-header__price {
  color: #333;
  grid-column: span 2;
  -ms-flex-item-align: center;
  align-self: center;
  margin: 0;
  font-size: 26px;
  font-weight: 700;
  line-height: 137%;
}

@media (min-width: 640px) {
  .bottom-header__price {
    grid-column: 3 / 5;
  }
}

@media (min-width: 1280px) {
  .bottom-header__price {
    grid-column: 5 / 7;
    margin-bottom: 4px;
  }
}

.bottom-header__offer {
  color: #4f4f4f;
  grid-column: span 2;
  -ms-flex-item-align: center;
  align-self: center;
  margin: 0 0 18px;
  font-size: 14px;
}

@media (min-width: 640px) {
  .bottom-header__offer {
    grid-column: 3 / 5;
    margin-bottom: 48px;
  }
}

@media (min-width: 1280px) {
  .bottom-header__offer {
    grid-column: 5 / 7;
  }
}

.bottom-header__button {
  --color-main-bg: #ffe339;
  --color-hover-bg: #ffec7a;
  grid-column: 1 / -1;
}

@media (min-width: 640px) {
  .bottom-header__button {
    grid-column: 2 / 4;
    width: 240px;
  }
}

@media (min-width: 1280px) {
  .bottom-header__button {
    grid-column: 4 / 7;
    width: 270px;
  }
}

.button {
  box-sizing: border-box;
  color: #121212;
  text-transform: uppercase;
  background: linear-gradient(to left, var(--color-main-bg) 50%, var(--color-hover-bg) 50%) right;
  background-size: 201%;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  height: 48px;
  font-family: Manrope, sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 150%;
  transition: all .3s ease-out;
  display: -ms-flexbox;
  display: flex;
}

.button:hover {
  border-left: var(--color-hover-bg);
  background-position: 0;
}

.button:after {
  content: "";
  background-image: url("arrow-buy-now.0e6613e3.svg");
  background-position: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  width: 28px;
  height: 8px;
  margin-left: 20px;
  transition: transform .3s;
}

.button:hover:after {
  transform: translateX(100%);
}

